export const markdownSubstitutionsMixin = {
  methods: {
    substituteMarkdownContent(content, firstName, lastName) {
      content = content.replace("->FIRST_NAME<-", firstName);
      content = content.replace("->LAST_NAME<-", lastName);
      content = content.replace("->NAME<-", firstName + " " + lastName);
      return content;
    }
  }
};
