<template>
  <p>
    You have been giving the opportunity to purchase
    <strong class="has-text-primary">
      {{ ticketOffer.ticketCount }}
    </strong>
    {{ plural ? "tickets at" : "ticket for" }}
    <strong class="has-text-primary">{{ formattedTicketPrice }}</strong>
    {{ plural ? "each" : null }}. There are
    <strong class="has-text-primary">{{ ticketsLeft }}</strong> tickets left,
    please complete the form below and buy your
    {{ plural ? "tickets" : "ticket" }} now.
  </p>
</template>

<script>
export default {
  name: "CopyTicketOffer",
  props: {
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  computed: {
    plural() {
      return this.ticketOffer.ticketCount > 1;
    },
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    ticketsLeft() {
      return this.event.standardTicketsLeft;
    }
  }
};
</script>
