<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-9">
          <h1 class="title is-1">Your Crew</h1>
          <h2 class="subtitle is-4">
            Buy tickets for or invite others
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-8">
          <p class="is-size-4">
            <span class="has-text-primary">{{ personName }}</span>
          </p>

          <CopyInviteAndBuy :allocationLeft="allocationLeft" :event="event" />

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-9">
          <div class="columns">
            <div class="column is-8">
              <div class="ticket-container">
                <h2 class="title is-3">Your Ticket Details</h2>

                <TicketPreview
                  :person="person"
                  :ticket-offer="ticketOffer"
                  :event="event"
                />
              </div>

              <div class="ticket-container columns">
                <div class="column">
                  <h2 class="title is-3">Invite Your Friends and Family</h2>

                  <NewInviteForm
                    :newInvite="newInvite"
                    :allocationReached="allocationReached"
                    @add-invite="addInvite($event)"
                  />
                </div>
              </div>
            </div>
            <div class="column is-4">
              <OrderTally
                :person="person"
                :invites="invites"
                :paidInviteCount="paidInviteCount"
                :ticketPrice="ticketOffer.costAfterDiscountCents * 1"
                @remove-invite="removeInvite($event)"
                @toggle-invite-paid="toggleInvitePaid($event)"
                @checkout="checkout"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import CopyInviteAndBuy from "@/ticketing/components/CopyInviteAndBuy";
import TicketPreview from "@/ticketing/components/TicketPreview";
import NewInviteForm from "@/ticketing/components/NewInviteForm";
import OrderTally from "@/ticketing/components/OrderTally";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";

export default {
  name: "TicketOfferPurchase",
  components: {
    CopyInviteAndBuy,
    TicketPreview,
    NewInviteForm,
    OrderTally
  },
  props: {
    person: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    },
    existingInvites: {
      type: Array,
      required: true
    },
    newInvite: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  data() {
    return {
      isRejectModalActive: false,
      invites: this.existingInvites,
      invitesOld: []
    };
  },
  computed: {
    paidInvites() {
      return this.invites.filter(invite => invite.paidFor);
    },
    paidInviteCount() {
      return this.paidInvites.length;
    },
    unPaidInvites() {
      return this.invites.filter(invite => !invite.paidFor);
    },
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    ticketsLeft() {
      return this.event.standardTicketsLeft;
    },
    allocationLeft() {
      return this.ticketOffer.ticketCount - 1 - this.invites.length;
    },
    allocationReached() {
      return this.allocationLeft <= 0;
    }
  },
  validations: {
    invites: {
      required,
      $each: {
        firstName: { required },
        lastName: { required },
        email: { required, email },
        paidFor: {}
      }
    }
  },
  methods: {
    addInvite(invite) {
      if (this.allocationReached) {
        return null;
      } else {
        TicketService.createInvite(this.event.slug, this.ticketOffer.id, invite)
          .then(res => {
            this.invites = res.data.invites;
          })
          .catch(e => {
            console.log("create invite error", e);
          });
      }
    },
    removeInvite(id) {
      TicketService.deleteInvite(id)
        .then(res => {
          this.invites = res.data.invites;
        })
        .catch(e => {
          console.log("delete invite error", e);
        });
      // this.invites.splice(index, 1);
    },
    toggleInvitePaid(id) {
      let invite = this.invites.find(invite => invite.id === id);
      TicketService.updateInvite(id, {
        ...invite,
        paidFor: !invite.paidFor
      })
        .then(res => {
          this.invites = res.data.invites;
        })
        .catch(e => {
          console.log("update invite error", e);
        });
    },
    checkout() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();

        TicketService.createStripeSession({
          ticketOfferId: this.ticketOffer.id,
          ticketsToPurchase: {
            guest_quantity: this.paidInviteCount
          }
        }).then(response => {
          const stripe = Stripe("pk_test_sGyh7aqViI3lKNlUR6iekPvd00LU1kLu8V");
          stripe
            .redirectToCheckout({
              sessionId: response.data.stripeSessionId
            })
            .then(result => {
              // If `redirectToCheckout` fails due to a browser or network
              // error, display the localized error message to your customer
              // using `result.error.message`.
            });
        });

        // TicketService.acceptTicket({
        //   id: this.ticketOffer.id,
        //   contactQuestions: this.person,
        //   ticketQuestions: {
        //     ...this.applicationQuestions,
        //     ...this.ticketQuestions
        //   }
        // })
        //   .then(response => {
        //     NProgress.done();
        //     window.location.assign(response.data.purchase_url);
        //   })
        //   .catch(e => {
        //     NProgress.done();
        //     console.log("ERROR", e);
        //   });
      }
    }
  }
};
</script>

<style scoped>
.add-button {
  margin-right: 1rem;
}
</style>
