<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">{{ event.pageTicketOfferTitle }}</h1>
          <h2 class="subtitle is-4">
            {{ event.pageTicketOfferSubtitle }}
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Hi
            <span class="has-text-primary">{{ personName }}</span>
          </p>

          <CopyTicketOffer :ticket-offer="ticketOffer" :event="event" />

          <Markdown :input="substitutedMarkdownContent" />

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="proceedToPurchase">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">General Questions</h2>
            <ApplicationQuestions
              class="section-spacing"
              v-model="applicationQuestions"
              :validations="$v.applicationQuestions"
            />

            <h2 class="title is-3">Questions for Ticket</h2>
            <TicketQuestions
              class="section-spacing"
              :questions="ticketQuestions"
              :$v="$v"
            />

            <div class="buttons has-addons is-right">
              <input
                class="button is-primary is-medium"
                type="submit"
                value="Proceed to Invites and Purchase"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Markdown from "@/components/Markdown";
import CopyTicketOffer from "@/ticketing/components/CopyTicketOffer";
import PersonInfo from "@/ticketing/components/PersonInfo";
import ApplicationQuestions from "@/ticketing/components/ApplicationQuestions";
import TicketQuestions from "@/ticketing/components/TicketQuestions.vue";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import {
  personQuestionsMixin,
  personQuestionsValidations,
  applicationQuestionsMixin,
  applicationQuestionsValidations,
  ticketQuestionsMixin,
  ticketQuestionsValidations
} from "@/mixins/questionsMixin";

export default {
  name: "TicketOfferFromApplication",
  components: {
    Markdown,
    CopyTicketOffer,
    PersonInfo,
    ApplicationQuestions,
    TicketQuestions
  },
  props: {
    personInit: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isRejectModalActive: false,
      substitutedMarkdownContent: this.substituteMarkdownContent(
        this.event.pageTicketOfferContentMarkdown,
        this.personInit.firstName,
        this.personInit.lastName
      )
    };
  },
  mixins: [
    personQuestionsMixin,
    applicationQuestionsMixin,
    ticketQuestionsMixin,
    personNameMixin,
    markdownSubstitutionsMixin
  ],
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    ticketsLeft() {
      return this.event.standardTicketsLeft;
    },
    ticketsLeftUrgent() {
      const lowTicketsNumber = 18;
      if (this.event.standardTicketsLeft < lowTicketsNumber) {
        return this.event.standardTicketsLeft;
      } else {
        return lowTicketsNumber;
      }
    }
  },
  validations() {
    if (this.ticketQuestions.foundedYesNo === 'true') {
      return {
        person: personQuestionsValidations,
        applicationQuestions: applicationQuestionsValidations,
        ticketQuestions: ticketQuestionsValidations[1]
      }
    } else {
      return {
        person: personQuestionsValidations,
        applicationQuestions: applicationQuestionsValidations,
        ticketQuestions: ticketQuestionsValidations[0]
      }
    }
  },
  methods: {
    proceedToPurchase() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();

        ProfileService.updateProfile(this.person.id, {
          person: {
            firstName: this.person.firstName,
            lastName: this.person.lastName,
            email: this.person.email
          },
          profile: {
            ...this.applicationQuestions,
            ...this.ticketQuestions
          }
        })
          .then(() => {
            this.$router.push({
              name: "ticket-offer-purchase",
              params: {
                eventSlug: this.event.slug,
                id: this.ticketOffer.id
              }
            });
            NProgress.done();
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });

        // TicketService.acceptTicket({
        //   id: this.ticketOffer.id,
        //   contactQuestions: this.person,
        //   ticketQuestions: {
        //     ...this.applicationQuestions,
        //     ...this.ticketQuestions
        //   }
        // })
        //   .then(response => {
        //     NProgress.done();
        //     window.location.assign(response.data.purchase_url);
        //   })
        //   .catch(e => {
        //     NProgress.done();
        //     console.log("ERROR", e);
        //   });
      }
    }
  }
};
</script>

